.modal-background {
    background-color:'#fff'; 
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    opacity: 0.8;
  }

  
  
  .flower-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 25%;
    height: 25%;
    background-position: top left;
    background-size: cover;
    /* background-image: url('flower-overlay-image-url'); Replace 'flower-overlay-image-url' with the actual URL of your flower overlay image */
    z-index: 1;
  }
  